<template>
    <div class="mainContainer">
     <br>
     <button @click="back" class="backButton btn btn-warning"> Back </button><br>
     <button @click="addFoodItems" class="add-food-button btn btn-success"> Add Food Items </button>
     <br>
      <div class="addFoodForm" v-if="showAddFoodForm">
        <h2>Add Food Items</h2>
        <br />
        <div>
          <b-form @submit.prevent="onSubmit" @reset="onReset" v-if="show">
            <b-form-group id="input-group-2" label="Name:" label-for="input-2">
              <b-form-input
                id="input-2"
                v-model="form.name"
                placeholder="Enter Food Name"
                required
              ></b-form-input>
            </b-form-group>
  
            <b-form-group id="input-group-3" label="Code" label-for="input-3">
              <b-form-input
                id="input-3"
                type="text"
                v-model="form.code"
                placeholder="Enter Food Code"
                required
              ></b-form-input>
            </b-form-group>
            <b-form-group id="input-group-3" label="Price" label-for="input-3">
              <b-form-input
                id="input-3"
                type="text"
                v-model="form.price"
                placeholder="Price"
                required
              ></b-form-input>
            </b-form-group>
  
            <b-button type="submit" variant="primary">Add Food Item</b-button>
          </b-form>
        </div>
      </div>
      <br />
      <div>
      </div>
    </div>
    <div>
      <h3 class="res-menu-title">Restaurant Menu </h3>
      <b-container class="cart-container">
        
              <b-card
                style="max-width: 20rem"
                class="mb-2 card-col"
                v-for="(item, index) in foodItems"
                :key="index"
              >
                <b-card-text>
                  <b> {{ item.name }}</b>
                </b-card-text>
                <b-card-text> <b>Price - </b> {{ item.price }} <br>
                  <b>Code - </b> {{ item.code }}
                </b-card-text>
            
                <!-- <button class="btn btn-sm btn-secondary">Add to Cart</button> -->
              </b-card>
        </b-container>
    </div>
  </template>
  
  <script>
  import { axiosInstance } from "./../api/axios";
  import { GET_FOOD_ITEMS } from "./../api/endpoints";
  export default {
    data() {
      return {
        foodItems: [],
        form: {
          name: "",
          code: "",
          price: "",
        },
        show: true,
        showAddFoodForm : false,
  
      };
    },
    methods: {
      getFoodItems() {
        axiosInstance.get(GET_FOOD_ITEMS).then((res) => {
          this.foodItems = res.data;
          console.log(res.data);
        });
      },
      back() {
        this.$router.push({
          name: "landing",
        });
      },
  
      onSubmit() {
        console.log(this.$data.form);
        this.foodItems.push(this.$data.form);
        this.$data.showAddFoodForm = false
      },
  
      addFoodItems(){
        this.$data.showAddFoodForm = !this.$data.showAddFoodForm;
      }
    },
    created() {
      this.getFoodItems();
      console.log(this.getFoodItems());
    },
  };
  </script>
  
  <style scoped>
