<template>
  <b-container class="container">
    <b-row>
      <b-col>
        <b-button @click="back" class="backButton"> Back </b-button>
      </b-col>
      <b-col class="addFoodItems">
        <b-button @click="addFoodItems"> Add Food Items </b-button>        
      </b-col>
    </b-row>
    <carousal-comp
      :images="resturantDetails?.images"
      :overview="resturantDetails?.overview"
      :contact="resturantDetails?.address"
    > 
 
  </carousal-comp>
  </b-container>
</template>
<script>
import { axiosInstance } from "./../api/axios";
import { GET_RESTURANT_DETAILS } from "./../api/endpoints";
import CarousalComp from "./CarousalComp.vue";
// import AddFoodItems from "./AddFoodItems.vue";
export default {
  components: {
    "carousal-comp": CarousalComp,
    // "add-food-items": AddFoodItems
  },
  props: ["code"],
  data() {
    return {
      resturantDetails: null,
    };
  },
  methods: {
    back() {
      this.$router.push({
        name: "landing",
      });
    },
    addFoodItems(){
      this.$router.push({
        name: "addfooditems",
      });
    },

    getRestuarantDetail() {
      const payload = {
        code: this.$props.code,
      };
      axiosInstance
        .get(GET_RESTURANT_DETAILS, { params: payload })
        .then((res) => {
          this.$data.resturantDetails = res.data;
        });
    },
  },
  created() {
    this.getRestuarantDetail();
  },
  beforeRouteEnter(to, from, next) {
    const store = from.matched[0].instances.default.$store;
    console.log(store);
    // await store.dispatch('load')
    next();
  },
};
</script>
<style>
.addFoodItems {
  text-align: end;
}

.container {
  margin-top: 15px;
}
</style>
